const theme = createMuiTheme({
  palette: {
    primary: green,
  },
});

type LoginInputProps = {
  name: string,
  type: string,
  text: string,
  setText: any,
};

const LoginInput = (props: LoginInputProps) => {
  const classes = useStyles();

  const handleChange = (event: any) => {
    props.setText(event.target.value);
  }
  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
      </ThemeProvider>
      <FormControl className={classes.margin}>
        <InputLabel shrink htmlFor="bootstrap-input">
          {props.name}
        </InputLabel>
        <BootstrapInput value={props.text} onChange={handleChange} type={props.type} autoComplete="off" placeholder={props.name} name={props.name} id="bootstrap-input" style={{ width: '100%' }} />
      </FormControl>
    </React.Fragment>

  );
}
export default LoginInput